body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.email-text {
  font-family: 'Urbanist', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  margin-top: 0px;
  margin-bottom: 20px;
  margin-right: 0px;
  margin-left: 0px;
  transition: 0.5s;
  -webkit-text-decoration: underline transparent;
          text-decoration: underline transparent;
}

.email-text:hover {
  font-family: 'Urbanist', sans-serif;
  font-style: normal;
  font-size: 19px;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  color:#2c98f0;
  text-decoration: underline;
  -webkit-text-decoration-color: #2c98f0;
          text-decoration-color: #2c98f0;
}

.email-text2 {
  font-family: 'Urbanist', sans-serif;
  font-style: normal;
  font-weight: 1100;
  font-size: 18px;
  align-items: stretch;
  margin-top: 50%;
  margin-bottom: 50%;
  margin-right: 0px;
  margin-left: 0px;
  color:black;
}

.icon-text {
  margin-top: 20%;
  margin-bottom: 5%;
  margin-right: 0px;
  margin-left: 0px;
  -webkit-transform: scale(1.5);
  transform:         scale(1.5);
  transition: 0.2s;
}

.icon-text:hover {
  margin-top: 23%;
  margin-bottom: 5%;
  margin-right: 0px;
  margin-left: 0px;
  -webkit-transform: scale(1.7);
  transform:         scale(1.7);
  transition: 0.3s;
}


.urbanist-500 {
  font-family: 'Urbanist', sans-serif;
  font-style: normal;
  font-weight: 500;
}

.box-1 {
  width:200px;
	height:200px;
	margin:10px;
	padding:10px;
  background:#c00;
}

.box-1 p {
  white-space:pre;
	font-size:smaller;
	font-family:monospace;
}

.urbanist-italics-500 {
  font-family: 'Urbanist', sans-serif;
  font-style: italic;
  font-weight: 500;
}

.urbanist-600 {
  font-family: 'Urbanist', sans-serif;
  font-style: normal;
  font-weight: 600;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

